(function(){
    $.ajaxSetup({
        headers: {
            'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content')
        }
    });
    $('.quanitySniper').on('change', function() {
        var id = $(this).data('id');
        var rowId = $(this).data('rowid');
        console.log(id, rowId);
        $.ajax({
          type: "PATCH",
          url: 'varukorg/' + rowId,
          data: {
            'quantity': this.value,
            'id': id,
          },
          success: function(data) {
            window.location.href = 'varukorg';
          }
        });
    });

     $('.DDET').on('change', function() {
        var id = $(this).data('id');
        var rowId = $(this).data('rowid');
        console.log(id, rowId);
        $.ajax({
          type: "PATCH",
          url: 'updateET',
          data: {
            'et': this.value,
            'rowId' : rowId,
            'id': id,
          },
          success: function(data) {
            window.location.href = 'varukorg';
          }
        });
    });
})();


